}

/* animation blink */

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  };
  25% {
    opacity: 0.25;
  };
  50% {
    opacity: 0.5;
  };
  60% {
    opacity: 0.6;
  };
  70% {
    opacity: 0.7;
  };
  75% {
    opacity: 0.75;
  }; 
  80% {
